            fill="#8a8a8a"
            p-id="2562"
          ></path>
          <path
            d="M320 341.333333h42.666667v384h-42.666667zM490.666667 341.333333h42.666666v384h-42.666666zM661.333333 341.333333h42.666667v384h-42.666667z"
            fill="#8a8a8a"
            p-id="2563"
          ></path>
        </svg>
      </div>
      <div class="delfoot" v-for="(k, index) in history" :key="index">
        <div class="foottext" @click="hsearch(k)">
          {{ k }}
        </div>
        <div class="foottu" @click="delhistory(index)">
