              d="M938.362063 510.457878c0-237.061161-192.174857-429.234995-429.247274-429.234995-237.062184 0-429.246251 192.173834-429.246251 429.234995 0 237.083673 192.185091 429.257507 429.246251 429.257507 97.345072 0 186.435133-33.110095 258.440074-87.677898 2.958378-3.354398 4.900613-7.636934 4.900613-12.449543 0-10.506285-8.521071-19.026332-19.027355-19.026332-5.431709 0-10.287297 2.162246-13.752212 5.826705l-0.2415 0c-64.456011 47.414893-143.745868 75.800383-229.876528 75.800383-214.679407 0-388.730489-174.073594-388.730489-388.719232 0-214.688617 174.051081-388.718209 388.730489-388.718209 214.688617 0 388.697743 174.029592 388.697743 388.718209 0 65.548902-15.386432 127.277802-44.081984 181.490517l0 0.309038c-0.508583 1.811252-1.104147 3.576455-1.104147 5.519714 0 10.507308 8.520047 19.028379 19.028379 19.028379 8.18952 0 15.054881-5.254677 17.703197-12.494569l0 0.132006C920.349827 648.38625 938.362063 581.536726 938.362063 510.457878z"
              p-id="2959"
              fill="#8a8a8a"
            ></path>
          </svg>
        </div>
      </div>
    </div>
    <div v-else style="margin-top: 75px">
      <div class="selectlist" v-for="(item, index) in selectlist" :key="index">
        <div class="listbox" @click="xq(item._id)">
          <div class="nr" v-if="item.imageSrc.length === 1">
            <div class="listtitle">{{ item.title }}</div>
            <div class="listimg">
              <img :src="item.imageSrc[0]" alt="" />
            </div>
          </div>
          <div class="nrs" v-else-if="item.imageSrc.length > 1">
            <div class="listtitle">{{ item.title }}</div>
            <div class="listimgs">
              <div v-for="(i, index) in item.imageSrc" :key="index">
                <img :src="i" alt="" />
              </div>
            </div>
          </div>
          <div class="nrss" v-else>
            <div class="listtitle">{{ item.title }}</div>
          </div>
          <div class="bigfoot">
            <div class="listfoot">
              <div>{{ item.author }}</div>
              <div>{{ item.comment }}评论</div>
              <div>{{ ontime(item.time) }}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { Search, List, Cell, Toast } from "vant";
export default {
  name: "search",
  components: {
    [Search.name]: Search,
    [List.name]: List,
    [Cell.name]: Cell,
  },
  data() {
    return {
      value: "",
      aShow: false,
      a: true,
      selectlist: [],
      history: [],
      mohusearch: false,
      mohulist: [],
    };
  },
  methods: {
    windowScroll(e) {
      document.querySelector(".van-field__control").blur();
        this.mohusearch = false;
      // console.log(e.view.scrollY);
      if (e.view.scrollY >= 1500) {
        this.aShow = true;
        // console.log(1);
      }else{
         this.aShow = false
      }
    },
    removea(){
      this.aShow = false
    },
    xq(id) {
      // console.log(id);
      this.$router.push({
        name: "lcontent",
        params: {
          id: id,
        },
      });
    },
    onSearch() {
      if (!this.value) return;
      this.value = this.value.trim();
      this.mohusearch = false;
      this.a = false;
      // console.log(111);
      // 搜索结果
      axios({
        method: "post",
        url: " https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/search",
        data: {
          skip: 0, //起始条数
          limit: 100, //一次返回的条数
          key_word: this.value, //关键字
        },
      }).then((res) => {
        // console.log(res.data.data);
        if (res.data.data.length == 0) {
          Toast("您要搜索的东西不存在");
        } else {
          // console.log(1);
          this.selectlist = res.data.data;
          // this.history.push(this.value.trim());
          this.value.length === 0 ? null : this.history.unshift(this.value);

          this.history = [...new Set(this.history)];

          localStorage.setItem("history", this.history);
        }
      });
    },
    clear() {
      this.a = true;
      this.mohusearch = false;
      // console.log(1);
    },
    input() {
      // console.log(111);
      if (this.value.trim().length === 0) {
        this.mohusearch = false;
        this.a = true;
      } else {
        // 搜索结果
        axios({
          method: "post",
          url: " https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/search",
          data: {
            // skip: 0, //起始条数
            // limit: 100, //一次返回的条数
            key_word: this.value, //关键字
          },
        }).then((res) => {
          // console.log(666);
          this.mohusearch = true;
          this.mohulist = res.data.data;
          if (this.value.trim().length === 0) {
            this.mohusearch = false;
            this.a = true;
          }
        });
      }
    },
    onCancel() {
      //   Toast('取消');
      this.$router.push("/index");
      // this.a = !this.a
    },
    delhistory(index) {
      this.history.splice(index, 1);
    },
    delall() {
      this.history.splice(0, this.history.length);
      localStorage.removeItem("history");
    },
    hsearch(k) {
      this.value = k;
      // console.log(k);
      this.onSearch();
    },
    mhsou(v) {
      // console.log(v);
      this.value = v;
      this.onSearch();
    },
    
  },
  activated() {
    this.aShow = false
    if (!localStorage.getItem("history")) {
      this.history = [];
    }
  },
  created() {
    this.history.push(
      localStorage.getItem("history")
        ? localStorage.getItem("history").split(",")
        : []
    );
    this.history = this.history[0];
  },
  beforeRouteEnter(to, from, next) {
    // console.log(to,from);
    if (from.path !== "/content") {
      setTimeout(() => {
        document.querySelector(".van-field__control").focus();
      }, 300);
    }else {
      next(vm=>{
        // console.log(vm);
        setTimeout(() => {
          if(window.scrollY>1500) {
            vm.aShow = true;
          }else {
            vm.aShow = false;
          }
        }, 200);
      })
    }
    next();
  },
};
</script>

